@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;700&display=swap);
.logo{display:flex;flex-direction:column;max-width:775px;width:100%;margin:70px 0 65px}@media screen and (max-width: 760px){.logo{margin:32px 28px 24px}}.logo__text{font-size:102px;line-height:90px;font-weight:bold;color:#fff}@media screen and (max-width: 500px){.logo__text{font-size:64px;line-height:64px}}@media screen and (max-width: 320px){.logo__text{font-size:32px;line-height:32px}}.logo__text--left{text-align:left}.logo__text--right{text-align:right}

.card{display:flex;flex-direction:column;width:100%;max-width:660px;min-height:524px;padding:50px 60px;background-color:#fff;box-shadow:0 4px 4px rgba(4,5,73,0.25),14px 14px 20px rgba(5,6,114,0.2);border-radius:8px}@media screen and (max-width: 760px){.card{padding:24px}}

.dropdown{max-width:252px;width:100%;height:48px;background:no-repeat right 0.7em top 50% url(/weather-forecast/static/media/chevron-bottom.a5408ea9.svg) rgba(128,131,164,0.06);border:2px solid rgba(128,131,164,0.06);padding:12px 16px;box-sizing:border-box;border-radius:8px;cursor:pointer;position:relative;overflow:visible;-webkit-appearance:none;-moz-appearance:none;appearance:none;text-align:left}.dropdown:hover{border:2px solid #8083A4}.dropdown:focus{outline:none;border:2px solid #373AF5;background:no-repeat right 0.7em top 50% url(/weather-forecast/static/media/chevron-top.01db9ee1.svg) rgba(128,131,164,0.06)}.dropdown__option-list{position:absolute;top:56px;left:0;width:252px;height:118px;background:#fff;border:2px solid rgba(128,131,164,0.06);box-shadow:0 2px 4px rgba(4,5,73,0.16),14px 14px 20px rgba(5,6,114,0.08);border-radius:8px;overflow-y:scroll;padding:0 6px;display:none;z-index:1000}.dropdown__option-list--visible{display:block}.dropdown__option-list::-webkit-scrollbar{background-color:transparent;width:4px}.dropdown__option-list::-webkit-scrollbar-thumb{width:4px;height:28px;background:rgba(128,131,164,0.4);border-radius:38px}.dropdown__option-list div{margin:6px 0}.dropdown__option-list div:hover{background:rgba(128,131,164,0.06);border-radius:8px}

.weekly-forecast-card__header{padding-bottom:5px}.weekly-forecast-card__dropdown{margin-top:20px}.weekly-forecast-card__mock-image{width:160px;height:160px;background-image:url("data:image/svg+xml;utf8,<svg width='100%' height='100%' xmlns='http://www.w3.org/2000/svg'><rect width='100%' height='100%' style='fill: none; stroke: grey; stroke-width: 3; stroke-dasharray: 8 3'/></svg>");margin-bottom:28px}.weekly-forecast-card__image-container{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:60px auto 0;width:100%;overflow:visible;position:relative}.weekly-forecast-card__forecast-container{display:grid;grid-template-columns:1fr 1fr 1fr;grid-column-gap:10px;width:100%;overflow:hidden;max-height:260px}@media screen and (max-width: 760px){.weekly-forecast-card__forecast-container{grid-template-columns:1fr}}.weekly-forecast-card__weather-icon{width:120px;height:120px}.weekly-forecast-card__arrow{position:absolute;width:24px;height:24px;top:50%;cursor:pointer}.weekly-forecast-card__arrow--left{left:-30px}@media screen and (max-width: 760px){.weekly-forecast-card__arrow--left{left:-25px}}.weekly-forecast-card__arrow--right{right:-30px}@media screen and (max-width: 760px){.weekly-forecast-card__arrow--right{right:-25px}}.weekly-forecast-card__arrow--disabled{cursor:auto;opacity:0.4}.weekly-forecast-card__weather-card{min-width:174px;min-height:260px !important}

.weather-card{background:#373AF5;width:100%;min-height:238px;position:relative;display:flex;justify-content:center;align-items:center}.weather-card__icon{width:160px;height:160px}.weather-card__temp{position:absolute;bottom:20px;right:20px}.weather-card__date{position:absolute;top:20px;left:20px}

.date-forecast-card__header{padding-bottom:5px}.date-forecast-card__mock-image{width:160px;height:160px;margin-bottom:28px}.date-forecast-card__image-container{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:60px auto 0;width:100%}.date-forecast-card__inputs-container{display:flex;justify-content:space-between;margin-top:20px;overflow:visible}@media screen and (max-width: 760px){.date-forecast-card__inputs-container{flex-direction:column}.date-forecast-card__inputs-container button:first-child{margin-bottom:24px}}

.date-picker{max-width:252px;width:100%;height:48px;background:no-repeat right 0.7em top 50% url(/weather-forecast/static/media/calendar.b59ccd47.svg) rgba(128,131,164,0.06);border:2px solid rgba(128,131,164,0.06);padding:12px 16px;box-sizing:border-box;border-radius:8px;cursor:pointer;position:relative;overflow:visible;-webkit-appearance:none;-moz-appearance:none;appearance:none;text-align:left}.date-picker:hover{border:2px solid #8083A4}.date-picker:focus{outline:none;border:2px solid #373AF5}.date-picker__option-list{position:absolute;top:56px;left:0;width:252px;height:118px;background:#fff;border:2px solid rgba(128,131,164,0.06);box-shadow:0 2px 4px rgba(4,5,73,0.16),14px 14px 20px rgba(5,6,114,0.08);border-radius:8px;overflow-y:scroll;padding:0 6px;display:none;z-index:1000}.date-picker__option-list--visible{display:block}.date-picker__option-list::-webkit-scrollbar{background-color:transparent;width:4px}.date-picker__option-list::-webkit-scrollbar-thumb{width:4px;height:28px;background:rgba(128,131,164,0.4);border-radius:38px}.date-picker__option-list div{margin:6px 0}.date-picker__option-list div:hover{background:rgba(128,131,164,0.06);border-radius:8px}

.main-page{display:flex;flex-direction:column;min-height:100vh;padding:16px 41px;background:no-repeat left bottom url("/weather-forecast/static/media/Academy-Weather forecast-Bg-buttom.071ea89a.png"),no-repeat right top url("/weather-forecast/static/media/Academy-Weather forecast-Bg-up-1.e73b4841.png"),#373AF5}@media screen and (max-width: 760px){.main-page{padding:10px}}.main-page__header{display:flex;justify-content:center;align-items:center}.main-page__main{display:flex;flex-grow:1;padding-bottom:46px;width:100%;justify-content:center;align-items:center}.main-page__main>article:first-child{margin-right:34px}@media screen and (max-width: 1440px){.main-page__main{flex-direction:column}.main-page__main>article:first-child{margin-right:0;margin-bottom:34px}}.main-page__footer{display:flex;justify-content:center;align-items:center}

*{margin:0;font-family:'Ubuntu', sans-serif;box-sizing:border-box}h1{font-size:32px;line-height:32px;font-weight:bold;color:#2C2D76;margin:5px 0}h2{font-size:24px;line-height:32px;font-weight:bold;color:#2C2D76}.text{font-size:16px;line-height:24px;font-weight:400;color:#2C2D76}.text--secondary{color:#8083A4}.text--weak{color:#fff}.text--bold{font-weight:bold}.text--small{font-size:14px;line-height:18px}.text--center{text-align:center}

